import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useFinanceDeletedLogsList } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import Loading from "../../../Loader/Loading";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import FinanceActivityLogsCard from "./FinanceActivityLogsCard";
const FinanceActivityLogs = ({ fid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { financeDeletedLogsList, financeDeletedLogsListLoading } =
    useFinanceDeletedLogsList({
      data: {
        fid: fid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !fid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeDeletedLogsList?.list?.length) {
      let obj = nextPage(financeDeletedLogsList?.list?.length);
      setState(() => obj);
    }
  }, [financeDeletedLogsList?.list]);
  const onSearchEvent = (e) => {
    if (
      !financeDeletedLogsListLoading &&
      financeDeletedLogsList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  // console.info("financeDeletedLogsList", financeDeletedLogsList);
  return (
    <>
      {/* <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("activity_logs")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
      
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper> */}
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>

      {financeDeletedLogsList?.list?.map((logs, index) =>
        financeDeletedLogsList?.list?.length === index + 1 ? (
          <div key={logs?._id} ref={ref}>
            <FinanceActivityLogsCard logs={logs} />
          </div>
        ) : (
          <FinanceActivityLogsCard logs={logs} key={logs?._id} />
        )
      )}
      {financeDeletedLogsListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeDeletedLogsList?.list?.length === 0 && (
          <EmptyMessage title="No logs found related this search." />
        )
      ) : (
        financeDeletedLogsList?.list?.length === 0 && (
          <EmptyMessage title="No logs." />
        )
      )}
    </>
  );
};
export default FinanceActivityLogs;
